import React, { useContext, useEffect, useState } from 'react';
import InfoTableContainer from './InfoTableContainer';
import { appContext } from './AppMain';
import TitleRow from './TitleRow';
import BasicTable from '../Components/BasicTable';
import { COLOR_PRIMARY_LIGHT, COLOR_WHITE } from '../utils/constants';
import TableForm from '../Components/TableForm';
import { FormControlLabel, Radio, RadioGroup } from '@mui/material';


const RADIO_DESIRED_LENGTH = "length";
const RADIO_DESIRED_ROWS = "rows";

const SleevesInfo = (props) => {
    let context = useContext(appContext);
    const [selectedDesiredValue, setSelectDesiredValue] = useState(RADIO_DESIRED_LENGTH);
    
      //calculate FB Equal rows & sts
      useEffect(() => {
        let sleeveStartSts = context.sleeveStartSts;
        let backStartSts = context.backStartSts;
        let frontStartSt = context.frontStartSt;

        // 앞단 식: 코 수(y) = 2x(단) + (frontStartSt * 2)(앞섶 시작 코수 양쪽 1씩)
        // 뒷판 식: 코 수(y) = x(단) + backStartSts(뒷판 시작 코 수)
        // ==> 뒤판이 앞판 양쪽 합과 같은 코수가 되는 식: 
        // 2x + (frontStartSt * 2) = x + backStartSts => 
        //  x(fbEqualRows) = backStartSts - (frontStartSt * 2)
        //  y(fbEqualSts) = x(fbEqualRows) + backStartSts
        let rows = backStartSts - (frontStartSt * 2);
        let sts = rows + backStartSts;
        context.setFbEqualSts(sts);
        context.setFbEqualRows(rows);

        // 소매 코수(한 쪽): sleeveStartSts += (rows * 2) 양쪽에서 늘리므로 단마다 2코씩 증가
        let sleeve = rows * 2;
        context.setSleeveSts(sleeve);

    }, [context.backStartSts, context.frontStartSt, context.fbEqualSts, context]);

    useEffect(() => {
        let afterRows = context.afterRows;

        if(selectedDesiredValue === RADIO_DESIRED_LENGTH) {
            let desiredRaglanLength = context.desiredRaglanLength;
            if(desiredRaglanLength > 0 && afterRows > 0) {
                let desiredRaglanRows = Math.round(desiredRaglanLength * (afterRows / 10));
                context.setDesiredRaglanRows(desiredRaglanRows);
            }
        } else if(selectedDesiredValue === RADIO_DESIRED_ROWS) {
            let desiredRaglanRows = context.desiredRaglanRows;
            if(desiredRaglanRows > 0 && afterRows > 0) {
                let desiredRaglanLength = Math.round(desiredRaglanRows / (afterRows / 10));
                context.setDesiredRaglanLength(desiredRaglanLength);
            }
        }
    }, [selectedDesiredValue, context, context.afterRows, context.desiredRaglanLength, context.desiredRaglanRows]);


    const handleDesiredRaglanLength = e => {
        context.setDesiredRaglanLength(e.target.value);
    };

    const handleChangeDesiredRaglanRows = e => {
        context.setDesiredRaglanRows(e.target.value);
    };

    const handleRadioChange = e => {
        setSelectDesiredValue(e.target.value);
    }


    const fbInfos = [
        {
            printDrection: "row",
            data: ["앞 뒤 콧수가 같아지는 단", "같아지는 콧수"],
        },
        {
            printDirection: "row",
            sx: {
                bgcolor: COLOR_PRIMARY_LIGHT,
                color: COLOR_WHITE,
            },
            data: [`+${context.fbEqualRows}단`, context.fbEqualSts],
        },
    ];

//"이 후 뒷판 콧수", "앞판 콧수"

    const fbEqualHeader = ["뒷판의 콧수와 앞판 두 개를 더한 콧수가 같을 때까지 뜨기(겉뜨기 단에서만 코 늘림)"];
    const fbHeader = ["첫 코와 마지막 코 늘림 없이 래글린 앞뒤만 늘리며 계속 뜨기"];

    const moreInfos = [
        {
            label: <FormControlLabel value={RADIO_DESIRED_LENGTH} 
                control={<Radio
                    sx={{'& .MuiSvgIcon-root': {
                        fontSize:17,
                    },}}
                 />} 
                 label="래글런 길이" />,
            txtInfo: [
                {
                    id: "desireRaglanLegnth",
                    value: context.desiredRaglanLength,
                    txtLabel: "원하는 길이를 입력하면 단 수가 아래에 계산됩니다.",
                    type: "number",
                    required: false,
                    onChange: handleDesiredRaglanLength,
                    endAdornment: "cm",
                    disabled: (selectedDesiredValue === RADIO_DESIRED_LENGTH) ? false : true,
                }
            ],
        },
        
        {
            label: <FormControlLabel value={RADIO_DESIRED_ROWS} 
                control={<Radio
                    sx={{'& .MuiSvgIcon-root': {
                        fontSize:17,
                    },}}
                />}  
                label="떠야하는 단 수" />,
            txtInfo: [
                {
                    id: "desiredRaglanRows",
                    value: context.desiredRaglanRows,
                    txtLabel: "원하는 단 수를 직접 입력해도 됩니다.",
                    type: "number",
                    required: false,
                    onChange: handleChangeDesiredRaglanRows,
                    endAdornment: "단",
                    disabled: (selectedDesiredValue === RADIO_DESIRED_ROWS) ? false : true,
                }
            ],
        },
    ];
  

    return (
        <React.Fragment>
            <InfoTableContainer>
                <TitleRow titles={fbEqualHeader} />
                {
                    fbInfos.map((info, idx) => {
                        return (<BasicTable key={idx} items={info.data} bg={info.bg} sx={info.sx} />)
                    })
                }
            </InfoTableContainer>

            <InfoTableContainer>
                <TitleRow titles={fbHeader} />
                <RadioGroup row
                aria-label="desiredRaglanValue"
                name="desiredRaglanValue"
                value={selectedDesiredValue}
                onChange={handleRadioChange}
                >

                {
                moreInfos.map((info, idx) => (
                    <TableForm key={idx} label={info.label} txtInfo={info.txtInfo} />
                ))
                }
            </RadioGroup>
            </InfoTableContainer>
        </React.Fragment>
    );
}


export default SleevesInfo;